






















export default {
	data() {
		return {
			list:''
		};
	},
	onShow(){
		// 获取订单列表
		var that= this;
		uni.showLoading({
			title:'加载中...'
		})
		uni.request({
			url:that.http+'order/getMyLease',
			method:'POST',
			header:that.header,
			data:{
				user_id:uni.getStorageSync('token')
			},
			success:(res)=>{
				uni.hideLoading()
				console.log(res)
				that.list = res.data.data
			},
			fail(){
				uni.showToast({
					title:'网络延迟，请刷新后重试!',
					icon:'none'
				})
			}
		})
	}
}
